@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-top: 2rem;
  margin: 0 auto;
}

body{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  /* background-color: rgb(242, 247, 248); */
  width: 100%;
  box-sizing: border-box;
}
#root{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  font-family: 'inter';
}


/* Each component */
.profile{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  margin-top: 30px;
}
.share_icon{
  position: absolute;
  width: 50px;
  top: 7%;
  right: 10%;
  cursor: pointer;
}
.btns{
  /* Frame 5 */

box-sizing: border-box;

padding: 24px 32px;
gap: 8px;

width: 1152px;
height: 76px;

background: #EAECF0;
/* background-color: red; */
/* Gray/200 */
border: 1px solid #EAECF0;
border-radius: 8px;


/* Text */

/* Text lg/Medium */
font-family: 'Inter';
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 28px;
/* identical to box height, or 156% */

/* Gray/900 */
color: #101828;
text-decoration: none;

}

.btns:hover{
  background: #D0D5DD;
/* Gray/300 */

border: 1px solid #D0D5DD;
/* Shadow/xs */

box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
}


.slack_git{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all .75s;
  margin-top: 1.5rem;
}

.slack_git img:hover{
  transform: scale(1.2);
}

.footer{
  display: flex;
  flex-direction: row;
  margin-top: 70px;
  width: 1200px;
  justify-content: space-around;
  align-items: center;
  color: #888888;
}

.footer img{
  width: 25%;
}

@media only screen and (max-width: 1200px) {
 #root,
 .App{
  width: 100%;
  margin: 0%;
 }

  .footer{
    width: 90%;
  }
}

@media only screen and (max-width: 1000px) {
  .btns{
    width: 880px;
  }
}
@media only screen and (max-width: 992px) {
  .btns{
    width: 720px;
  }
}

@media only screen and (max-width: 830px) {
  .btns{
    width: 620px;
  }
}
@media only screen and (max-width: 720px) {
  .btns{
    width: 520px;
  }
}
@media only screen and (max-width: 580px) {
  .btns{
    width: 480px;
  }
}
@media only screen and (max-width: 520px) {
  .btns{
    width: 400px;
  }
  .footer{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 1rem;
    padding-bottom: 3rem;
  }
  .footer img{
    width: 45%;
  }
}
@media only screen and (max-width: 400px) {
  .btns{
    width: 375px;
  }
}
@media only screen and (max-width: 375px) {
  body{
    min-width: 375px;
  }
  .btns{
    min-width: 100%;
  }
}


